<template>
  <div class="foodMenuSearch">
    <div class="header">
      <div class="iconfont icon-jiantouzuo goBack-icon" @click="gotoBack()"></div>
      <div class="tp-search">
        <div class="tp-search-box">
          <div class="iconfont icon-sousuo"></div>
          <input
            type="text"
            v-model="tableNo"
            class="inputCla"
            @change="searchFun()"
            placeholder="菜品检索"
          >
          <div class="iconfont icon-quxiao" @click="deletInput" v-show="showDelet"></div>
        </div>
        <div class="tp-right">
          <span class="search-btn" @click="searchFun()">搜索</span>
        </div>
      </div>
    </div>
    <div class="body">
      <div class="dish-name-list" v-if="this.newMenuArr.length>0">
        <div class="dish-name-item" v-for="(item, index) in this.newMenuArr" :key="index">
          <div class="dish-name-left">
            <p class="dish-name-left-up">{{item.pluDescript}}</p>
            <p class="dish-name-left-down">
              <span class="dish-name-price">￥{{item.pluPrice}}</span>/ 位
            </p>
          </div>
          <div class="dish-name-right">
            <template v-if="item.itemClasslist.length>0">
              <div class="taste-btn icon-btn" @click="tasteClickFun(item)">
                口味
                <span v-if="item.num>0" class="taste-btn-tips">{{item.num}}</span>
              </div>
            </template>
            <template v-else-if="item.num>0">
              <div class="cut-btn" @click="cutNumFun(item,index)">
                <i class="iconfont icon-jian"></i>
              </div>
              <span class="num">{{item.num}}</span>
              <div class="add-btn icon-btn" @click="addNumFun(item,index)">
                <i class="iconfont icon-jia1"></i>
              </div>
            </template>
            <template v-else>
              <div class="add-btn icon-btn" @click="addNumFun(item,index)">
                <i class="iconfont icon-jia1"></i>
              </div>
            </template>
          </div>
        </div>

        <!-- <div class="dish-name-item">
          <div class="dish-name-left">
            <p class="dish-name-left-up">美式咖啡卡布奇洛</p>
            <p class="dish-name-left-down">
              <span class="dish-name-price">￥25</span>/ 位
            </p>
          </div>
          <div class="dish-name-right">
            <div class="add-btn icon-btn">
              <i class="iconfont icon-jia1"></i>
            </div>
          </div>
        </div>
        <div class="dish-name-item">
          <div class="dish-name-left">
            <p class="dish-name-left-up">美式咖啡卡布奇洛</p>
            <p class="dish-name-left-down">
              <span class="dish-name-price">￥25</span>/ 位
            </p>
          </div>
          <div class="dish-name-right">
            <div class="taste-btn icon-btn">口味</div>
          </div>
        </div>
        <div class="dish-name-item">
          <div class="dish-name-left">
            <p class="dish-name-left-up">美式咖啡卡布奇洛</p>
            <p class="dish-name-left-down">
              <span class="dish-name-price">￥25</span>/ 位
            </p>
          </div>
          <div class="dish-name-right">
            <div class="taste-btn icon-btn">
              口味
              <span class="taste-btn-tips">55</span>
            </div>
          </div>
        </div>
        <div class="dish-name-item">
          <div class="dish-name-left">
            <p class="dish-name-left-up">美式咖啡卡布奇洛</p>
            <p class="dish-name-left-down">
              <span class="dish-name-price">￥25</span>/ 位
            </p>
          </div>
          <div class="dish-name-right">
            <div class="cut-btn">
              <i class="iconfont icon-jian"></i>
            </div>
            <span class="num">2</span>
            <div class="add-btn icon-btn">
              <i class="iconfont icon-jia1"></i>
            </div>
          </div>
        </div>-->
      </div>
      <div v-else-if="this.newMenuArr.length==0 && this.isStartSearch" class="no-list">
        <div class="no-result-icon">
          <i class="iconfont icon-jiazaishibai"></i>
        </div>
        <p class="no-result-text">未查到结果</p>
      </div>
    </div>
    <foodTaste v-if="isShowTaste" :itemTasteObj="itemTasteObj" @hidepage="hidepage($event)"></foodTaste>
  </div>
</template>
<script>
import { mapState } from "vuex";
import foodTaste from "../common/foodTaste";
import { Indicator, Toast } from "mint-ui";
export default {
  components: {
    foodTaste
  },
  data() {
    return {
      tableNo: "",
      showDelet: false,
      debounceTask: 500,
      newMenuArr: [],
      isShowTaste: false,
      itemTasteObj: {},
      isStartSearch: false
    };
  },
  computed: {
    ...mapState({
      seatsInfo: state => state.food.seatsInfo,
      MenuArr: state => state.food.MenuArr
    })
  },
  mounted() {
    console.log("seatsInfo", this.seatsInfo);
  },
  methods: {
    //input删除键
    deletInput() {
      this.tableNo = "";
    },

    gotoBack() {
      this.$router.push("/foodMenu");
    },
    searchFun() {
      if (this.debounceTask !== undefined) clearTimeout(this.debounceTask);
      this.debounceTask = setTimeout(() => {
        this.serachData(this.tableNo);
      }, 500);
    },
    serachData(val) {
      this.isStartSearch = false;
      this.$http
        .get("/ohotel/router", {
          params: {
            method: "getPosInterfacePluAll",
            pccode: this.seatsInfo.pccode,
            needPage: "false",
            searchEle: val
          },
          vv: 5
        })
        .then(res => {
          if (res.data.code == "0") {
            //console.log("res.data.data", res.data.data);
            if (res.data.data) {
              res.data.data.forEach((element, indexs) => {
                let isIn = false;
                this.MenuArr.forEach(ele => {
                  if (element.pluCode == ele.pluCode) {
                    //res.data.data[indexs] = _.cloneDeep(ele);
                    if (!element.num) {
                      element.num = 0;
                    }
                    element.num += ele.num;
                    isIn = true;
                  }
                });
                if (isIn == false) {
                  this.$set(element, "num", 0);
                }
              });
              // console.log("res.data.data", res.data.data);
              this.newMenuArr = res.data.data;
            } else {
              this.isStartSearch = true;
              this.newMenuArr = [];
            }
          } else {
            this.msg(res.data.msg);
          }
        });
    },
    cutNumFun(item, indexs) {
      if (this.newMenuArr[indexs].num > 0) {
        this.newMenuArr[indexs].num--;
        this.$store.commit("cutMenu", item);
      }
    },
    addNumFun(item, indexs) {
      console.log("item:", item);
      this.newMenuArr[indexs].num++;
      this.$store.commit("addMenu", item);
    },
    tasteClickFun(item, index) {
      this.itemTasteObj = item;
      this.isShowTaste = true;
      //console.log(" this.itemTasteObj:", this.itemTasteObj);
    },
    hidepage(val) {
      this.isShowTaste = false;
      let isIn = false;
      if (val) {
        this.newMenuArr.forEach((ele, index) => {
          if (ele.pluCode == val.pluCode) {
            ele.num += val.num;
            isIn = true;
          }
        });
        if (!isIn) {
          this.newMenuArr.push(_.cloneDeep(val));
        }
        this.$store.commit("addMenu", val);
      }
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    }
  },
  watch: {
    tableNo() {
      if (this.tableNo) {
        this.showDelet = true;
      } else {
        this.showDelet = false;
      }
    }
  }
};
</script>
<style lang="less" scoped>
.foodMenuSearch {
  .header {
    padding-left: 0.347rem;
    padding-right: 11px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #393c41;
    .goBack-icon {
      color: #fff;
    }
    .tp-search {
      flex: 1;
      display: flex;
      justify-content: space-between;
      flex-direction: row;
      padding-left: 0.373rem;
      background-color: #393c41;

      .tp-search-box {
        flex: 1;
        height: 0.853rem;
        margin-top: 0.211rem;
        margin-bottom: 0.211rem;
        display: flex;
        justify-content: flex-start;
        flex-direction: row;
        align-items: center;
        background: #f6f8f9;
        border-radius: 1.3rem;
        .icon-sousuo {
          color: #cccccc;
          margin-left: 0.27rem;
          font-size: 0.37rem;
        }
        .icon-quxiao {
          color: #cccccc;
          font-size: 0.32rem;
          margin-right: 0.27rem;
        }

        .inputCla {
          border: none;
          background: #f6f8f9;
          outline: none;
          font-size: 0.32rem;
          width: 85%;
          margin-left: 0.27rem;
        }
      }
      .tp-right {
        margin-left: 0.4rem;
        .search-btn {
          line-height: 4;
          color: #fff;
        }
      }
    }
  }
  .body {
    padding: 0.133rem 0.133rem;
    .dish-name-list {
      .dish-name-item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: #fff;
        margin-bottom: 0.133rem;
        padding-right: 0.32rem;
        border-radius: 3px;
        .dish-name-left {
          padding: 0.133rem 0.32rem 0.133rem 6px;

          .dish-name-left-up {
            font-size: 0.427rem;
            color: #333333;
            text-align: left;
          }
          .dish-name-left-down {
            text-align: left;
            font-size: 0.32rem;
            color: #a2a8bd;
            display: flex;
            align-items: center;
            .dish-name-price {
              font-size: 0.427rem;
              color: #f7544d;
              margin-right: 11px;
            }
          }
        }
        .dish-name-right {
          width: 2.4rem;
          display: flex;
          align-items: center;
          justify-content: flex-end;
          .icon-btn {
            color: #fff;
            background: #ff7b02;
          }
          .add-btn {
            width: 0.507rem;
            height: 0.507rem;
            line-height: 0.507rem;
            border-radius: 2.667rem;
            .iconfont {
              font-size: 0.32rem;
            }
          }
          .cut-btn .iconfont {
            font-size: 0.48rem;
            color: #ff7b02;
            font-weight: 800;
          }
          .num {
            display: inline-block;
            width: 33px;
            font-size: 19.2px;
            color: #77a0ff;
          }

          .taste-btn {
            width: 38px;
            height: 0.64rem;
            font-size: 0.32rem;
            line-height: 0.64rem;
            border-radius: 2.667rem;
            position: relative;
            .taste-btn-tips {
              position: absolute;
              top: -0.267rem;
              right: -0.133rem;
              display: inline-block;
              width: 0.453rem;
              height: 0.453rem;
              line-height: 0.453rem;
              border-radius: 0.267rem;
              background: #d0021b;
            }
          }
        }
      }
    }
    .no-list {
      font-size: 0.32rem;
      .no-result-icon {
        text-align: center;
        padding-top: 2.667rem;
        .iconfont {
          font-size: 1.28rem;
        }
      }
      .no-result-text {
        text-align: center;
      }
    }
  }
}
</style>



